import { Component } from '@angular/core';

@Component({
  selector: 'logo',
  templateUrl: './logo.component.html',
  styleUrls: ['./logo.component.css']
})
export class LogoComponent {
  static instance = null;
  ngOnInit(){
	  if( null == LogoComponent.instance ){
			LogoComponent.instance = this;
	  }
  }
}
