import { Component } from '@angular/core';
import { Input } from '@angular/core';
import { OnInit } from '@angular/core';
import { AfterContentInit } from '@angular/core';

@Component({
  selector: 'movingpicture',
  templateUrl: './movingpicture.component.html',
  styleUrls: ['./movingpicture.component.css']
})
export class MovingPictureComponent implements AfterContentInit{
	
	@Input() id: string;
	@Input() src: string;
	@Input() imageWidthPx: number;
	@Input() imageHeightPx: number;
	@Input() travelDirection: string;
	@Input() travelPercent: number;
	
	ngAfterContentInit(){
		var c = document.getElementById(this.id + "Container");
		var i = document.getElementById(this.id + "Image");
		alert(this.id + "Image");
		alert(c); 
		alert(i);
	}
}
